<template>
  <div>
    <v-header :seller="seller"></v-header>
    <div class="tab border-1px">
      <div class="tab-item">
        <router-link v-bind:to="'/goods'">商品</router-link>
      </div>
      <div class="tab-item">
        <router-link to="/ratings">评论</router-link>
      </div>
      <div class="tab-item">
        <router-link to="/seller">商家</router-link>
      </div>
    </div>
    <keep-alive>
      <router-view :seller="seller"></router-view>
    </keep-alive>
  </div>
</template>

<script>
import header from './components/header/header.vue'
import data from './assets/json/data.json'
export default {
  data () {
    return {
      seller: {}
    }
  },
  created () {
    this.seller = data.seller
    console.log(data)
  },
  components: {
    'v-header': header
  }
}
</script>

<style>
  .tab{display:flex;width:100%;height:40px;line-height:40px;position:relative;}
  .tab .tab-item{flex:1;text-align:center;}
  .tab .tab-item a{display:block;font-size:14px;color:rgb(77,85,93);}
  .tab .tab-item .active{color:rgb(240,20,20);}
</style>



