<template>
  <div class="star">
    <div class="star-item star-48" :class="starType">
      <span v-for="itemClass in itemClasses" :class="itemClass" class="star-item"></span>
    </div>
  </div>
</template>

<script>
  const LENGTH = 5
  const CLS_ON = 'on'
  const CLS_HALF = 'half'
  const CLS_OFF = 'off'
  export default {
    props: {
      size: {
        type: Number
      },
      score: {
        type: Number
      }
    },
    computed: {
      starType () {
        return 'star-' + this.size
      },
      itemClasses () {
        let result = []
        let score = Math.floor(this.score * 2) / 2
        let hasDecimal = score % 1 !== 0
        let integer = Math.floor(score)
        for (let i = 0; i < integer; i++) {
          result.push(CLS_ON)
        }
        if (hasDecimal) {
          result.push(CLS_HALF)
        }
        while (result.length < LENGTH) {
          result.push(CLS_OFF)
        }
        return result
      }
    }
  }
</script>
<style>
  .star{font-size: 0;}
  .star .star-item{display: inline-block;background-repeat: no-repeat;}
  .star .star-item.star-48 .star-item{width: 20px;height: 20px;margin-right: 0;background-size: 20px 20px;background: url("star24_on@2x.png");}
  .star .star-item.star-48 .star-item:last-child{margin-right: 0;}
</style>



