<template>
  <div class="cartControl">
    <transition name="fade">
      <div class="cart-decrease" v-show="food.count>0" @click.stop.prevent="decreaseCart($event)">
        <transition name="inner">
          <span class="inner iconfont icon-jian">+</span>
        </transition>
      </div>
    </transition>
    <span class="cart-count" v-show="food.count>0">{{food.count]}</span>
    <span class="iconfont icon-jia cart-add" @click.stop.prevent="addCart($event)"></span>
  </div>
</template>

<script>
  import Vue from 'vue'
  export default {
    props: {
      food: {
        type: Object
      }
    },
    methods: {
      addCart (event) {
        if (!event._constructed) {
          return
        }
        if (!this.food.count) {
          Vue.set(this.food, 'count', 1)
        } else {
          this.food.count++
        }
        this.$emit('increment', event.target)
      },
      decreaseCart (event) {
        if (!event._constructed) {
          return
        }
        this.food.count--
      }
    }
  }
</script>

<style>
  .cartControl{font-size: 0;}
  .cartControl .cart-decrease{padding: 4px 6px 6px 6px;}
  .cartControl .cart-decrease .inner{display: inline-block;line-height: 24px;font-size: 24px;vertical-align: top;color: #000;}
  .cartControl .cart-add{background-size: 30px 30px;display: inline-block;padding: 6px;line-height: 24px;font-size: 24px;vertical-align: top;color: #000;background: url("../../assets/images/img-1.png") 50% 50% no-repeat;}
</style>



