<template>
  <div class="goods">
    <div class="menu-wrapper" ref="menuWrapper">
      <ul>
        <li v-for="(item, index) in goods" class="menu-item border-1px" :class="{'current':currentIndex === index}">
          <span class="text">
            <span class="icon" v-show="item.type>0"></span>{{item.name}}
          </span>
        </li>
      </ul>
    </div>
    <div class="foods-wrapper" ref="foodWrapper">
      <ul>
        <li v-for="item in goods" class="food-list food-list-hook">
          <h1 class="title">{{item.name}}</h1>
          <ul>
            <li v-for="food in item.foods" class="food-item" @click="selectFood(food, $event)">
              <div class="icon">
                <img :src="food.icon" alt="" width="57" height="57">
              </div>
              <div class="content">
                <h2 class="name">{{food.name}}</h2>
                <p class="desc">{{food.description}}</p>
                <div class="extra">
                  <span class="count">月售{{food.sellCount}}</span>
                  <span class="count">好评{{food.rating}}</span>
                </div>
                <div class="price">
                  <span class="now">￥{{food.price}}</span>
                  <span class="old" v-show="food.oldPrice">{{food.oldPrice}}</span>
                </div>
                <div class="cartControl-wrapper">
                  <cartControl :food="food"></cartControl>
                </div>
              </div>
            </li>
          </ul>
        </li>
      </ul>
    </div>
    <shopCart></shopCart>
  </div>
</template>

<script>
  import data from '../../assets/json/data.json'
  import BScroll from 'better-scroll'
  import shopCart from '../shopcart/shopcart.vue'
  import cartControl from '../cartcontrol/cartcontrol.vue'
  export default{
    props: {
      seller: {
        type: Object
      }
    },
    data () {
      return {
        goods: [],
        listHeight: [],
        scrolly: 0,
        selectedFood: {}
      }
    },
    created () {
      this.goods = data.goods
      this.$nextTick(() => {
        this._initScroll()
        this._calculateHeighe()
      })
      this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee']
    },
    computed: {
      currentIndex () {
        for (let i = 0; i < this.listHeight.length; i++) {
          let height = this.listHeight[i]
          let height2 = this.listHieght[i + 1]
          if (!height2 || (this.scrolly >= height && this.scrolly < height2)) {
            return i
          }
        }
        return 0
      }
    },
    methods: {
      _initScroll () {
        this.menuScroll = new BScroll(this.$refs.menuWrapper, {
          click: true
        })
        this.foodScroll = new BScroll(this.$refs.foodWrapper, {
          probeType: 3,
          click: true
        })
        this.foodScroll.on('scroll', (pos) => {
          this.scroll = Math.abs(Math.round(pos.y))
        })
      },
      _calculateHeighe () {
        let foodList = this.$refs.foodWrapper.getElementsByClassName('food-list-hook')
        let height = 0
        this.listHeight.push(height)
        for (let i = 0; i < foodList.legnth; i++) {
          let item = foodList[i]
          height += item.clientHeight
          this.listHeight.push(height)
        }
      }
    },
    components: {
      shopCart,
      cartControl
    }
  }
</script>

<style>
  .goods{color: #000;margin-top: 1px;display:flex;position: absolute;width: 100%;top: 174px;bottom: 46px;overflow: hidden;z-index: 99;}
  .goods .menu-wrapper{flex: 0 0 80px;width: 80px;background: #f3f5f7;}
  .goods .menu-wrapper ul{transition-duration: 0ms;transform: translate(0px, 0px) translateZ(0px);}
  .goods .menu-wrapper .menu-item{display:table;width:56px;height:54px;line-height:14px;padding:0 12px;}
  .goods .menu-wrapper .menu-item .text{font-size:12px;display:table-cell;width:56px;vertical-align: middle;position:relative;font-size:12px;}
  .goods .menu-wrapper .menu-item .text .icon{color:#000;}
  .goods .menu-wrapper .menu-item.current{position:relative;z-index:10;margin-top:-1px;background:#fff;font-weight:700;}
  .goods .foods-wrapper{flex: 1;}
  .goods .foods-wrapper .food-list .title{padding-left: 24px;height: 26px;line-height: 26px;border-left: 2px solid #d9dde1;font-size: 12px;color: rgb(147,153,159);background: #f3f5f7;}
  .goods .foods-wrapper .food-list .food-item{display: flex;margin: 18px;padding-bottom: 18px;position: relative;border-bottom: 1px solid rgba(7,17,27,0.1);}
  .goods .foods-wrapper .food-list .food-item .icon{flex: 0 0 57px;margin-right: 10px;}
  .goods .foods-wrapper .food-list .food-item .content{flex: 1;}
  .goods .foods-wrapper .food-list .food-item .content .name{margin: 2px 0 8px 0;font-size: 14px;line-height: 14px;height: 14px;color: #07111b;}
  .goods .foods-wrapper .food-list .food-item .content .desc{margin-bottom: 8px;line-height: 12px;font-size: 10px;color: #93999f;}
  .goods .foods-wrapper .food-list .food-item .content .extra{line-height: 12px;font-size: 12px;color: #93999f;}
  .goods .foods-wrapper .food-list .food-item .content .price{line-height: 27px;}
  .goods .foods-wrapper .food-list .food-item .content .price .now{margin-right: 8px;font-size: 14px;color: red;}
  .cartControl-wrapper{position: absolute;right: 0;bottom: 12px;width: 20px;height: 26px;line-height: 26px;text-align: center;}
</style>



