<template>
  <div class="header">
    <div class="content-wrapper">
      <div class="avatar">
        <img width="64" height="64" :src="seller.avatar">
      </div>
      <div class="content">
        <div class="title">
          <span class="brand"></span>
          <span>{{seller.name}}</span>
        </div>
        <div class="description">
          {{seller.description}}/{{seller.deliveryTime}}分钟送达
        </div>
        <div class="support">
          <span class="icon"></span>
          <span class="text">{{seller.supports[0].description}}</span>
        </div>
      </div>
      <div class="supports-count" v-if="seller.supports" @click="showDetail">
        <span class="count">{{seller.supports.length}}个&gt;</span>
        <i class="icon iconfont icon-zuoyoujiantou"></i>
      </div>
    </div>
    <div class="bulletin-wrapper" @click="showDetail">
      <span class="bulletin-title"></span><span class="bulletin-text">粥品香坊其烹饪粥料的秘方源于中国千年古法，在融和现代制作工艺，由世界烹饪大师屈浩先生领衔研发。坚守纯天然、0添加的良心品质深得消费者青睐，发展至今成为粥类的引领品牌。是2008年奥运会和2013年园博会指定餐饮服务商。</span>
      <i class="icon iconfont icon-zuoyoujiantou"></i>
    </div>
    <div class="background">
      <img alt="" class="" width="100%" height="100%" :src="seller.avatar">
    </div>
    <transition name="fade">
      <div class="detail" transition="fade" v-show="detailShow" @click="hideDetail">
        <div class="detail-wrapper clearFix">
          <div class="detail-main">
            <h1 class="name">{{seller.name}}</h1>
            <div class="star-wrapper">
             <star></star>
            </div>
            <div class="title">
              <div class="line"></div>
              <div class="text">优惠信息</div>
              <div class="line"></div>
            </div>
            <ul  class="supports" v-if="seller.supports">
              <li class="supports-item" v-for="(item, index) in seller.supports">
                <span class="icon" :class="classMap[seller.supports[index].type]"></span>
                <span class="text">{{seller.supports[index].description}}</span>
              </li>
            </ul>
            <div class="title">
              <div class="line"></div>
              <div class="text">商家公告</div>
              <div class="line"></div>
            </div>
            <div class="bulletin">
              <p class="content">{{seller.bulletin}}</p>
            </div>
          </div>
        </div>
        <div class="detail-close" >
          <i class="iconfonts icon-cha"><span class="iconname">X</span></i>
        </div>
      </div>
    </transition>
  </div>
</template>

  <script>
    import star from '../star/star.vue'
    export default {
      props: {
        seller: {
          type: Object
        }
      },
      data () {
        return {
          detailShow: false
        }
      },
      methods: {
        showDetail () {
          this.detailShow = true
        },
        hideDetail () {
          this.detailShow = false
        }
      },
      created () {
        this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee']
      },
      components: {
        star
      }
    }
  </script>
<style>
  .header{position:relative;background:rgba(7,17,27,0.5);overflow:hidden;z-index:999;}
  .brand{background: url("brand@2x.png") center center no-repeat;display: inline-block;vertical-align: top;width: 30px;height: 18px;background-size: 30px 18px;}
  .header .content-wrapper{position:relative;padding:24px 12px 18px 24px;color:#fff;}
  .count{font-size: 12px;}
  .header .content-wrapper .avatar{display:inline-block;vertical-align:top;}
  .header .content-wrapper .content{display:inline-block;font-size:14px;margin-left:16px;}
  .header .content-wrapper .supports-count{position:absolute;right:12px;bottom:14px;padding:0 8px;line-height:24px;border-radius:12px;background:rgba(0,0,0,0.2);}
  .header .content-wrapper .content .title{margin:2px 0 8px 0;}
  .description{margin-bottom:5px;}
  .support .text{font-size:12px;}
  .bulletin-wrapper{position:relative;background-color: rgba(1,17,27,0.2);height:28px;padding:0 22px 0 12px;white-space:normal;overflow: hidden;text-overflow:ellipsis;line-height:28px;}
  .header .bulletin-wrapper .bulletin-text{vertical-align:top;margin:0 4px;font-size:12px;color: #fff;}
  .header .background{position:absolute;top:0;left:0;width:100%;height:100%;z-index:-1;filter:blur(10px);}
  /*点击弹出隐藏商家信息样式*/
  .header .detail{position:fixed;height:100%;top:0;left:0;width:100%;overflow:auto;background:rgba(7,17,27,0.8);color:#fff;z-index:99999;}
  .header .detail.fade-enter-active{animation: bounce-in .4s;}
  .header .detail.fade-leave-active{animation: bounce-out .6s;}
  @keyframes  bounce-in{
    0%{
      transform:  scale(0);
    }
    50%{
      transform: scale(1.5);
    }
    100%{
      transform: scale(1);
    }
  }
  @keyframes bounce-out {
    0%{
      transform: scale(1);
    }
    50%{
      transform: scale(1.5);
    }
    100%{
      transform: scale(0);
    }
  }
  .clearFix{display:inline-block;width:100%;}
  .detail-wrapper .detail-main{margin-top:64px;padding-buttom:64px;}
  .detail-wrapper .detail-main .name{text-align:center;line-height:16px;font-size:16px;}
  .detail-wrapper .detail-main .star-wrapper{margin-top:18px;padding:2px 0;text-align:center;}
  .detail-wrapper .detail-main .title{width:80%;display:flex;margin:28px auto 0 auto;}
  .detail-wrapper .detail-main .title .line{flex:1;position:relative;top:-6px;border-bottom:1px solid rgba(255,255,255,0.2);}
  .detail-wrapper .detail-main  .supports{width:80%;margin:22px auto;}
  .detail-wrapper .detail-main  .supports .supports-item{padding:0 12px;font-size:12px;margin-bottom: 12px;}
  .detail-wrapper .detail-main .bulletin{width:80%;height:200px;margin:22px auto;}
  .detail-wrapper .detail-main .bulletin .content{line-height:25px;padding:0 12px;font-size:12px;}
  .detail-close{position:relative;width:32px;height:32px;margin:-64px auto 0 auto;clear: both;font-size:32px;}
  .iconfonts{font-size:30px;display:inline-block;width:40px;height:40px;border-radius:50%;border:2px solid #fff;position:relative;}
  .iconname{position:absolute;left:24%;top:9%;font-size:29px;}
  .detail .detail-wrapper .detail-main .supports .icon{display: inline-block;width: 16px;height: 16px;vertical-align: top;margin-right: 6px;background-size: 16px 16px;background-repeat: no-repeat;}
  .decrease{background: url("decrease_1@2x.png");}
  .discount{background: url("discount_1@2x.png");}
  .special{background: url("special_1@2x.png");}
  .invoice{background: url("invoice_1@2x.png");}
  .guarantee{background: url("guarantee_1@2x.png");}
</style>



